function setAttr(domNode, name, value) {
	name = (name === "className") ? "class" : name;

	if (value != null && value !== "") {
		if (typeof value === "boolean") {
			if (value === false) {
				domNode.removeAttribute(name);
			}
			else {
				domNode.setAttribute(name, "");
			}
		}
		else {
			domNode.setAttribute(name, value);
		}
	}
}

export default setAttr;
