import isString from "lodash-es/isString";
import setAttrs from "./setAttrs";

function createElement(type, attrs, ...children) {
	if (type == null) {
		return "";
	}

	let domNode = document.createElement(type);

	attrs = (attrs == null) ? {} : attrs;
	children = (children == null) ? [] : children;
	setAttrs(domNode, attrs);

	children
		.map(child => isString(child) ? document.createTextNode(child) : child)
		.forEach(child => domNode.appendChild(child));

	return domNode;
}

export default createElement;
