import removeClass from "./removeClass";

function removeClassesStartingWithButNot(domNode, str) {
	for (let i = domNode.classList.length - 1; i >= 0; i--) {
		let currentClass = domNode.classList.item(i);

		if (currentClass !== str && currentClass.startsWith(str)) {
			removeClass(domNode, currentClass);
		}
	}
	return domNode;
}

export default removeClassesStartingWithButNot;
